import React,{useEffect, useState} from 'react'
import { useNavigate} from 'react-router-dom'
import {Form, Button, Row, Col, Table, } from 'react-bootstrap'
import {LinkContainer} from 'react-router-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import Loader from '../components/Loader'
import Message from '../components/Message'
import {getUserDetails,updateUserProfile } from '../actions/userActions'
import {USER_UPDATE_PROFILE_RESET} from '../constants/userContants'
import {listMyOrders} from '../actions/orderActions'

function ProfileScreen() {
    const [name, setName]= useState('')
    const [email, setEmail]= useState('')
    const [password, setPassword]= useState('')
    const [confirmPassword, setConfirmPassword]= useState('')
    const [message, setMessage]= useState('')

    const dispatch = useDispatch()
    const navigate = useNavigate();


    const userDetails = useSelector(state =>state.userDetails)
    const {error, loading, user} = userDetails

    const userLogin = useSelector(state =>state.userLogin)
    const {userInfo} = userLogin


    const userUpdateProfile  = useSelector(state =>state.userUpdateProfile )
    const {success} = userUpdateProfile 

    const orderListMy  = useSelector(state =>state.orderListMy)
    const {loading:loadingOrders, error:errorOrders, orders} = orderListMy 

    useEffect(()=>{
        if(!userInfo){
            navigate('/login')
        }else{
            if(!user || !user.name || success || userInfo._id !== user._id){
                dispatch({type:USER_UPDATE_PROFILE_RESET})
                dispatch(getUserDetails('profile'))
                dispatch(listMyOrders())
            }else{
                setName(user.name)
                setEmail(user.email)
            }
        }
    },[dispatch, navigate, userInfo, user, success])

    const submitHandler =(e)=>{
        e.preventDefault()
        if(password !== confirmPassword){
            setMessage('password do not match')
        }else{
            dispatch(updateUserProfile({
                'id':user._id,
                'name':name,
                'email':email,
                'password':password
            }))
        }
    }



  return (
    <Row>
        <Col md={3}>
            <h2>Votre profile</h2>
        {message && <Message variant='danger'>{message}</Message> }
        {error && <Message variant='danger'>{error}</Message>}
        {loading && <Loader/>}
        <Form onSubmit={submitHandler}>
            {/* name form */}
            <Form.Group controlId='name'>
                <Form.Label>Nom</Form.Label>
                <Form.Control
                    required
                    type='name'
                    placeholder='Entrer nom'
                    value={name}
                    onChange={(e)=> setName(e.target.value)}
                    >
                </Form.Control>
            </Form.Group>

           {/* email form */}
            <Form.Group controlId='email'>
                <Form.Label>Email Addresse</Form.Label>
                <Form.Control
                    required
                    type='email'
                    placeholder='Entrer email'
                    value={email}
                    onChange={(e)=> setEmail(e.target.value)}
                    >
                </Form.Control>
            </Form.Group>
            
            {/*password form */}
                <Form.Group controlId='password'>
                <Form.Label>Password</Form.Label>
                <Form.Control
                    type='password'
                    placeholder='Entrer Mot de pass'
                    value={password}
                    onChange={(e)=> setPassword(e.target.value)}
                    >
                </Form.Control>
            </Form.Group>

            {/*confirmPassword form */}
            <Form.Group controlId='passwordConfirm'>
                <Form.Label>Confirm Password</Form.Label>
                <Form.Control
                    type='password'
                    placeholder='Confirm Password'
                    value={confirmPassword}
                    onChange={(e)=> setConfirmPassword(e.target.value)}
                    >
                </Form.Control>
            </Form.Group>
            {/* button register */}
            <Button className='mt-2 d-grid gap-2 rounded' type='submit' variant="success">Modifier votre compte</Button>
        </Form>
        </Col>

        <Col md={9}>
            <h2> Mes Commandes</h2>
            {loadingOrders ? (
                <Loader/>
            ):errorOrders ? (
                <Message variant='danger'>{errorOrders}</Message>
            ): (
                <Table striped responsive className='table-sm'>
                <thead>
                    <tr>
                        <th>ID</th>
                        <th>Date</th>
                        <th>Total</th>
                        <th>Paye</th>
                        <th>EXPÉDITION</th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>{orders.map(order =>(
                    <tr key={order._id}>
                        <td>{order._id}</td>
                        <td>{order.createdAt.substring(0,10)}</td>
                        <td>{order.totalPrice}€</td>
                        <td>{order.isPaid ? order.paidAt.substring(0,10):(
                            <i className='fas fa-times' style={{color:'red'}}></i>
                        )}
                        </td>
                        <td>
                            <LinkContainer to={`/order/${order._id}`}>
                                <Button variant="success" className='d-grid gap-2 rounded btn-sm'> Voir Detail</Button>
                            </LinkContainer>
                        </td>
                    </tr>
                ))}</tbody>
                </Table>
            )}
        </Col>
    </Row>
  )
}

export default ProfileScreen
